.app-work {
  width: 80%;
  margin: 0 auto;
}
.app-work-main {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}
.app-work-main .item {
  padding: 0.5rem 0.4rem;
  flex: 0 1 25%;
  cursor: pointer;
}
.app-work-main .item-wrap {
  border: 1px solid rgba(4, 8, 9, 0.5);
  padding: 1rem 0 0.6rem;
  text-align: center;
  min-height: 180px;
}
.app-work-main .item-wrap img {
  width: 90%;
  margin-top: 0.5rem;
}
.app-work-main .item-footer {
  margin-top: 0.1rem;
  cursor: pointer;
}
.app-work-main .item-footer:before,
.app-work-main .item-footer:after {
  content: '';
  display: inline-block;
  border-radius: 50%;
  background-color: #040809;
  margin: 0 0.1rem;
  padding: 0.04rem;
}
.app-work-main .item .border-top {
  margin-top: -1.15rem;
  text-align: center;
}
.app-work-main .item .border-top > div {
  display: inline-block;
}
.app-work-main .item .border-top .space {
  width: 0.4rem;
  height: 0.2rem;
  background-color: #fff;
  margin: 0 0.2rem;
}
.app-work-main .item .border-top .square-wrap {
  padding: 0 0.2rem;
  background-color: #fff;
}
.app-work-main .item .border-top .square {
  border: 1px solid rgba(4, 8, 9, 0.5);
  padding: 0.04rem;
  background-color: #fff;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.app-work-main .item .border-top .square .in {
  background-color: rgba(4, 8, 9, 0.7);
  width: 0.1rem;
  height: 0.1rem;
}
.app-work-main .item.active .item-wrap,
.app-work-main .item:hover .item-wrap {
  border: 1px solid #e71810;
}
.app-work-main .item.active .border-top .square,
.app-work-main .item:hover .border-top .square {
  border: 1px solid #e71810;
}
.app-work-main .item.active .border-top .square .in,
.app-work-main .item:hover .border-top .square .in {
  background-color: #e71810;
}
@media screen and (min-width: 1500px) {
  .app-work-main .item {
    flex: 0 1 20%;
  }
  .app-work-main .item .border-top .space {
    width: 0.2rem;
  }
  .app-work-main .item-wrap {
    padding: 1rem 0 0.4rem;
  }
  .app-work-main .item-wrap img {
    margin-top: 0.4rem;
  }
}
